"use client"
import React, { useEffect, useState } from 'react'

interface Option {
   id:number;
   name:string;
  }
const Filing = () => {
  const[estbOptions,setEstbOPtions]=useState<Option[]>([]);
  const [estbValue,setEstbValue]=useState('');
  // const[casetype,setCasetype]=useState('');

  useEffect(()=>{
     const fetchEstbData=async ()=>{
      const token=localStorage.getItem("authToken");
      try 
      {
        const fetchResponse=await fetch("http://15.206.60.189/erp/masters/category/",{
          headers:{
            "Content-Type": "application/json",
            "Authorization":`Token ${token}`
          },
        });
        const fetchdata: Option[]=await fetchResponse.json();
        setEstbOPtions(fetchdata);
      }
      catch(error)
      {
        console.error("Error fetching options:", error);
      }
     }
     fetchEstbData();
  },[]);

  return (
     <>
        <div className="card">
  <div className="card-header bg-dark text-white">
   Case Details
  </div>
  <div className="card-body">
        <div className="row">
          <div className="col-sm-3">
            <label htmlFor="ddl_establishment" className="control-label"><b>Establishment</b></label>
            <select name="ddl_establishment" id="ddl_establishment" className="form-select form-select-sm" onChange={(e)=>setEstbValue(e.target.value)} value={estbValue}>
               <option value="">Select</option>
              {
                estbOptions.map((option)=>(
                  <option key={option.id} value={option.id}>{option.name}</option>
                ))
              }
            </select>
          </div>
          <div className="col-sm-3">
            <label htmlFor="ddl_casetype" className="control-label"><b>Case Type</b></label>
            <select name="ddl_casetype" id="ddl_casetype" className="form-select form-select-sm">
               <option value="">Select</option>
            </select>
          </div>
        </div>
  </div>
</div> 
     </>
  )
}

export default Filing
